import React, { useState,useRef } from 'react';
import Player from './components/Player';
import Song from './components/Song';
import './styles/app.scss';
import data from './data';
import Library from "./components/Library";
import Nav from "./components/Nav";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faVolumeMute, faVolumeUp } from '@fortawesome/free-solid-svg-icons';


function App() {
  const [songs, setSongs] = useState(data());
  const [currentSong, setCurrentSong] = useState(songs[0]);
  const [isPlaying, setIsPlaying] = useState(false);
  const [songInfo,setSongInfo]= useState ({
    currentTime:0,
    duration:0,
    animationPercentage:0,
  });
  const audioRef = useRef(null);
  const [libraryStatus,setLibraryStatus] = useState(false);


  const timeUpdateHandler = (e) => {
    const current= e.target.currentTime;
    const duration= e.target.duration;
    const roundedCurrent = Math.round(current);
    const roundedDuration = Math.round(duration);
    const animation = Math.round((roundedCurrent / roundedDuration)*100)
    
    setSongInfo({
        ...songInfo, currentTime:current, duration:duration, animationPercentage:animation
    })
}
  const [mute, setMute] = useState(false);

const songEndHandler= async() =>{
  let currentIndex = songs.findIndex((song) => song.id === currentSong.id);
          await  setCurrentSong(songs[(currentIndex + 1) % songs.length]);
          if(isPlaying) audioRef.current.play();
};

const muteHandler = () => {
  setMute(!mute);
};

const volumeHandler = (e) => {
  audioRef.current.volume = e.target.value / 100
};



  return (




    <div className={`App ${libraryStatus? 'library-active':''}`}>

      < Nav
      libraryStatus={libraryStatus}
      setLibraryStatus={setLibraryStatus}
      />


      <Song currentSong={currentSong}
      songInfo={songInfo}
      isPlaying={isPlaying}
      audioRef={audioRef}
      setSongInfo={setSongInfo}

      
      />

      <Player
       audioRef={audioRef}
       currentSong={currentSong}
       isPlaying={isPlaying}
       setIsPlaying={setIsPlaying}
       setSongInfo={setSongInfo}
       songInfo={songInfo}
       setCurrentSong={setCurrentSong}
       songs={songs}
       setSongs={setSongs}
        
       />
       <div className="volume-container">
        <button onClick={muteHandler} className="mute">
          <FontAwesomeIcon icon={mute ? faVolumeMute : faVolumeUp} />
        </button>
        <div className="bar-container">
           <input onChange={volumeHandler} id="volume" type="range" />
        </div>
      </div>

       <Library
         
         songs={songs}
         setCurrentSong={setCurrentSong}
         audioRef={audioRef}
         isPlaying={isPlaying}
         setSongs={setSongs}
         libraryStatus={libraryStatus}
         />

         <audio
          onLoadedMetadata={timeUpdateHandler}
          onTimeUpdate={timeUpdateHandler}
          ref={audioRef}
          src={currentSong.audio}
          onEnded={songEndHandler}
          muted={mute}

          ></audio>

    </div>

  );

}

export default App;
